import { motion } from "framer-motion";
import { timeCalculation, certificationRatings } from "../helper";
import styled from "styled-components";
import { Buttons } from "../Routes/Home";

export const Info = styled(motion.div)`
  position: absolute;
  width: 100%;
  bottom: -7.2rem;
  height: 7.5rem;
  padding: 0.8rem;
  border-radius: 0 0 10px 10px;
  background-color: ${(porps) => porps.theme.black};
  opacity: 0;
  @media screen and (min-width: 1700px) {
    bottom: -7.4rem;
  }
  @media screen and (min-width: 1900px) {
    bottom: -7.4rem;
  }
  @media screen and (min-width: 2000px) {
    bottom: -7.4rem;
  }
`;
export const Playing = styled.span`
  margin-right: 0.5rem;
  opacity: 1;
  &:hover {
    opacity: 0.8;
  }
`;
export const Check = styled.span`
  margin-right: 0.5rem;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
`;
export const More = styled.span`
  position: absolute;
  right: 0;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
`;
export const Thumbs = styled.span`
  cursor: pointer;
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
  border-radius: 1.5rem;
  border: solid 2.5px white;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
`;
export const DetailsWrap = styled.div`
  position: relative;
  width: 100%;
  margin: 0 auto;
  height: 60%;
  top: -1.5rem;
  overflow: hidden;
`;
export const MaturityNRuntime = styled.div`
  display: flex;
  justify-content: left;
  align-items: center;
  position: absolute;
  top: 0;
  height: 50%;
  width: 100%;
  img {
    height: 1.5rem;
    margin-right: 0.5rem;
  }
  span {
    font-size: 0.7rem;
  }
`;
export const Genres = styled.ul`
  text-transform: capitalize;
  padding: 0.8rem 0;
  position: absolute;
  top: 0;
  height: 50%;
  width: 100%;
  color: ${(props) => props.theme.white};
  font-size: 0.7rem;
  li {
    line-height: inherit;
    overflow-wrap: break-word;
    float: left;
    margin-right: 0.5rem;
  }
  span {
    display: inline-block;
    margin-right: 0.5rem;
    width: 0.3rem;
    height: 0.3rem;
    border-radius: 10px;
    background-color: #545454;
  }
  li:first-child {
    span {
      display: none;
    }
  }
`;

export const InfoVariants = {
  hover: {
    opacity: 1,
    display: "block",
    transition: { delay: 0.5, type: "tween", duration: 0 },
    boxShadow: "0 10px 20px rgba(0, 0, 0, 0.8)",
  },
};

function MovieInfo({ maturityRating, runtime, genres, keywords }) {
  return (
    <Info variants={InfoVariants}>
      <Buttons
        style={{
          width: "100%",
          margin: "0 auto",
          position: "relative",
          top: "-0.8rem",
        }}
      >
        <Playing>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="2rem"
            height="2rem"
            fill="currentColor"
            viewBox="0 0 16 16"
          >
            <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM6.79 5.093A.5.5 0 0 0 6 5.5v5a.5.5 0 0 0 .79.407l3.5-2.5a.5.5 0 0 0 0-.814l-3.5-2.5z" />
          </svg>
        </Playing>
        <Check>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="2rem"
            height="2rem"
            fill="currentColor"
            viewBox="0 0 16 16"
          >
            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
            <path d="M10.97 4.97a.235.235 0 0 0-.02.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-1.071-1.05z" />
          </svg>
        </Check>
        <Thumbs
          style={{
            width: "2rem",
            height: "2rem",
            borderRadius: "1rem",
            border: "solid 2.3px white",
            marginTop: "0.7rem",
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="2rem"
            height="3rem"
            fill="currentColor"
            viewBox="-6 3 32 32"
          >
            <path d="M8.864.046C7.908-.193 7.02.53 6.956 1.466c-.072 1.051-.23 2.016-.428 2.59-.125.36-.479 1.013-1.04 1.639-.557.623-1.282 1.178-2.131 1.41C2.685 7.288 2 7.87 2 8.72v4.001c0 .845.682 1.464 1.448 1.545 1.07.114 1.564.415 2.068.723l.048.03c.272.165.578.348.97.484.397.136.861.217 1.466.217h3.5c.937 0 1.599-.477 1.934-1.064a1.86 1.86 0 0 0 .254-.912c0-.152-.023-.312-.077-.464.201-.263.38-.578.488-.901.11-.33.172-.762.004-1.149.069-.13.12-.269.159-.403.077-.27.113-.568.113-.857 0-.288-.036-.585-.113-.856a2.144 2.144 0 0 0-.138-.362 1.9 1.9 0 0 0 .234-1.734c-.206-.592-.682-1.1-1.2-1.272-.847-.282-1.803-.276-2.516-.211a9.84 9.84 0 0 0-.443.05 9.365 9.365 0 0 0-.062-4.509A1.38 1.38 0 0 0 9.125.111L8.864.046zM11.5 14.721H8c-.51 0-.863-.069-1.14-.164-.281-.097-.506-.228-.776-.393l-.04-.024c-.555-.339-1.198-.731-2.49-.868-.333-.036-.554-.29-.554-.55V8.72c0-.254.226-.543.62-.65 1.095-.3 1.977-.996 2.614-1.708.635-.71 1.064-1.475 1.238-1.978.243-.7.407-1.768.482-2.85.025-.362.36-.594.667-.518l.262.066c.16.04.258.143.288.255a8.34 8.34 0 0 1-.145 4.725.5.5 0 0 0 .595.644l.003-.001.014-.003.058-.014a8.908 8.908 0 0 1 1.036-.157c.663-.06 1.457-.054 2.11.164.175.058.45.3.57.65.107.308.087.67-.266 1.022l-.353.353.353.354c.043.043.105.141.154.315.048.167.075.37.075.581 0 .212-.027.414-.075.582-.05.174-.111.272-.154.315l-.353.353.353.354c.047.047.109.177.005.488a2.224 2.224 0 0 1-.505.805l-.353.353.353.354c.006.005.041.05.041.17a.866.866 0 0 1-.121.416c-.165.288-.503.56-1.066.56z" />
          </svg>
        </Thumbs>
        <More>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="2rem"
            height="2rem"
            fill="currentColor"
            viewBox="0 0 16 16"
          >
            <path
              fillRule="evenodd"
              d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8zm15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v5.793L5.354 8.146a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0l3-3a.5.5 0 0 0-.708-.708L8.5 10.293V4.5z"
            />
          </svg>
        </More>
      </Buttons>
      <DetailsWrap>
        <MaturityNRuntime>
          {certificationRatings(maturityRating)}
          <span>{timeCalculation(runtime)}</span>
        </MaturityNRuntime>
        <Genres>
          {keywords &&
            keywords.slice(0, 1).map((keyword) => (
              <li key={keyword.id}>
                <span></span>
                {keyword.name}
              </li>
            ))}
          {genres &&
            genres.slice(0, 2).map((genre) => (
              <li key={genre.id}>
                <span></span>
                {genre.name}
              </li>
            ))}
        </Genres>
      </DetailsWrap>
    </Info>
  );
}

export default MovieInfo;
